import {GaiaSession} from "@/session/GaiaSession"
import {BaseTemplateType} from "@/core/ast/template/BaseTemplate"


import {CustomTemplate} from "@/preset/template/common/CustomTemplate"
import {FadeTemplate} from "@/preset/template/common/FadeTemplate"
import {WipeTemplate} from "@/preset/template/common/WipeTemplate"
import {MoveTemplate} from "@/preset/template/common/MoveTemplate"
import {ZoomTemplate} from "@/preset/template/common/ZoomTemplate"
import {TypeTemplate} from "@/preset/template/text/TypeTemplate"
import {CutTemplate} from "@/preset/template/common/CutTemplate"
import {GrowTemplate} from "@/preset/template/common/GrowTemplate"
import {ChangeColorTemplate} from "@/preset/template/common/ChangeColorTemplate"
import {MoveFromTemplate} from "@/preset/template/common/MoveFromTemplate"
import {MoveToTemplate} from "@/preset/template/common/MoveToTemplate"

import {BarLabelEnterTemplate} from "@/preset/template/bar-chart/BarLabelEnterTemplate"

import {PathBlinkTemplate} from "@/preset/template/path/PathBlinkTemplate"
import {CircleTemplate} from "@/preset/template/annotation/CircleTemplate"
import {WheelTemplate} from "@/preset/template/common/WheelTemplate"


export function getPresetTemplates(session: GaiaSession): BaseTemplateType[] {
  return [
    /* Common */
    new CustomTemplate(),
    new FadeTemplate(),
    new WipeTemplate(),
    new MoveTemplate(),
    new ZoomTemplate(),
    new TypeTemplate(),
    new CutTemplate(),
    new GrowTemplate(),
    new ChangeColorTemplate(),
    new MoveFromTemplate(),
    new MoveToTemplate(),
    new WheelTemplate(),

    /* Path */
    new PathBlinkTemplate(),

    /* Bar Chart */
    new BarLabelEnterTemplate(),

    /* Annotation */
    new CircleTemplate(),
  ]
}
