import {DataType, TargetModel, VirtualElementType} from "@/core/ast/target/TargetModel"
import assert from "assert"
import {getSvgBox} from "@/core/utils/SvgUtils"

/**
 * Base Target model that uses SVG
 */
export abstract class BaseSVGTargetModel extends TargetModel<SVGElement> {
  protected constructor(origin: SVGSVGElement) {
    super(origin)
  }

  processIdSet() {
    // set id for all elements
    const elements = this.origin.querySelectorAll<SVGElement>('*')
    elements.forEach(el => {
      const id = el.getAttribute('id')
      if (id != null && id != '') {
        this.realElementIdSet.add(id)
      }
    })
    let idCnt = 0
    elements.forEach(el => {
      if (el.id == null || el.id == '') {
        if (this.realElementIdSet.has(`el-${idCnt}`)) {
          idCnt++
        }
        el.id = `el-${idCnt}`
        this.realElementIdSet.add(el.id)
        idCnt++
      }
    })
  }

  getOrigin(): SVGSVGElement {
    return this.origin as SVGSVGElement
  }

  saveOriginalData(el: SVGGraphicsElement, name: string, value: any) {
    const el2 = el as (SVGGraphicsElement & {_original: {[key: string]: any}})
    if (el2._original == null) {
      el2._original = {}
    }
    el2._original[name] = value
  }

  getOriginalData(el: SVGGraphicsElement, name: string) {
    const el2 = el as (SVGGraphicsElement & {_original: {[key: string]: any}})
    if (el2._original == null) {
      return null
    }
    return el2._original[name]
  }

  saveOriginalBox(el: SVGGraphicsElement) {
    const box = this.getElBox(el)
    this.saveOriginalData(el, 'box', box)
  }

  getOriginalBox(el: SVGGraphicsElement) {
    return this.getOriginalData(el, 'box')
  }

  getBox(id: string): { x: number; y: number; height: number; width: number } {
    const el = this.getRealElementById(id)
    assert(el instanceof SVGGraphicsElement, `Can only get box of SVGGraphicsElement, but got ${el.constructor.name}.`)
    return getSvgBox(this.getOrigin(), el)
  }

  getElBox(el: SVGGraphicsElement) {
    return getSvgBox(this.getOrigin(), el)
  }

  getDefs(): SVGDefsElement {
    if (this.origin.querySelector('defs') == null) {
      this.origin.appendChild(document.createElementNS('http://www.w3.org/2000/svg', 'defs'))
    }
    return this.origin.querySelector('defs')
  }

  createVirtualElement(el: SVGElement, id: string, classList: string[], data: DataType = {}): VirtualElementType {
    const vEl = Object.assign(el.cloneNode(true) as SVGElement, {data})
    // console.log('>>>', vEl, vEl.data)
    vEl.setAttribute('id', id)
    vEl.setAttribute('class', classList.join(' '))
    vEl.setAttribute(TargetModel.VIR_EL_ATTR, '')
    // mount data to attributes
    for (const [key, value] of Object.entries(data)) {
      if (vEl.hasAttribute(key)) {
        console.warn(`Attribute ${key} already exists in ${vEl}. Change it to data-${key}`)
        vEl.setAttribute(`data-${key}`, String(value))
      } else {
        vEl.setAttribute(key, String(value))
      }
    }
    // vEl.setAttribute('data', JSON.stringify(data))
    return vEl
  }

  getRealElementById(id: string): SVGElement {
    return this.origin.querySelector(`#${id}`)
  }
}
