import {Synthesizer} from "@/core/exec/synthesis/Synthesizer"
import {CompileExec} from "@/session/CompileExec"
import {SynthesisRule} from "@/core/exec/synthesis/SynthesisRule"
import {MappingRule} from "@/core/exec/synthesis/rules/MappingRule"
import {GsapAnimation} from "@/preset/gsap/GsapPhysicalAnimation"
import {BaseLogicalAniType} from "@/core/ast/animation/logical/LogicalAnimation"
import {PhysicalAnimation} from "@/core/ast/animation/physical/PhysicalAnimation"
// import {gsap} from "gsap"


import {SyncImpl} from "@/preset/gsap/impl/common/SyncImpl"
import {ConcatImpl} from "@/preset/gsap/impl/common/ConcatImpl"
import {CustomImpl} from "@/preset/gsap/impl/svg/common/CustomImpl"
import {FadeImpl} from "@/preset/gsap/impl/svg/common/FadeImpl"
import {WipeImpl} from "@/preset/gsap/impl/svg/common/WipeImpl"
import {MoveImpl} from "@/preset/gsap/impl/svg/common/MoveImpl"
import {ZoomImpl} from "@/preset/gsap/impl/svg/common/ZoomImpl"
import {TypeImpl} from "@/preset/gsap/impl/svg/text/TypeImpl"
import {CutImpl} from "@/preset/gsap/impl/svg/common/CutImpl"
import {GrowImpl} from "@/preset/gsap/impl/svg/common/GrowImpl"
import {ChangeColorImpl} from "@/preset/gsap/impl/svg/common/ChangeColorImpl"

import {PathBlinkImpl} from "@/preset/gsap/impl/svg/path/PathBlinkImpl"

import {CircleImpl} from "@/preset/gsap/impl/svg/annotation/CircleImpl"
import {WheelImpl} from "@/preset/gsap/impl/svg/common/WheelImpl"


export class GsapSynthesizer extends Synthesizer<SVGElement, GsapAnimation> {
  constructor(compileExec: CompileExec<SVGElement, GsapAnimation>,
              extraRules: SynthesisRule<SVGElement, BaseLogicalAniType, GsapAnimation>[] = []) {
    super(compileExec, extraRules)
  }

  getDefaultRules(): SynthesisRule<SVGElement, BaseLogicalAniType, GsapAnimation>[] {
    return [
      /* Common */
      new MappingRule("gaia.common.Sync", SyncImpl),
      new MappingRule("gaia.common.Concat", ConcatImpl),
      new MappingRule("gaia.common.Custom", CustomImpl),
      new MappingRule("gaia.common.Fade", FadeImpl),
      new MappingRule("gaia.common.Wipe", WipeImpl),
      new MappingRule("gaia.common.Move", MoveImpl),
      new MappingRule("gaia.common.Zoom", ZoomImpl),
      new MappingRule("gaia.common.Cut", CutImpl),
      new MappingRule("gaia.common.Grow", GrowImpl),
      new MappingRule("gaia.common.ChangeColor", ChangeColorImpl),
      new MappingRule("gaia.common.Wheel", WheelImpl),

      /* Text */
      new MappingRule("gaia.text.Type", TypeImpl),

      /* Path */
      new MappingRule("gaia.path.PathBlink", PathBlinkImpl),

      /* Annotation */
      new MappingRule("gaia.annotation.Circle", CircleImpl),
    ]
  }

  generate(physical: PhysicalAnimation<SVGElement, BaseLogicalAniType, GsapAnimation>): GsapAnimation {
    // gsap.defaults({immediateRender: false})
    return super.generate(physical)
  }
}
