import {gsap} from "gsap"
import {SynthesisContext} from "@/core/exec/synthesis/SynthesisContext"
import {GsapAnimation, GsapPhysicalAnimation} from "@/preset/gsap/GsapPhysicalAnimation"
import {EffectAnimation} from "@/core/ast/animation/logical/impl/EffectAnimation"
import {CustomPropSchema} from "@/preset/template/common/CustomTemplate"


export class CustomImpl extends GsapPhysicalAnimation<SVGElement, EffectAnimation<typeof CustomPropSchema>> {
  doGenerate(cxt: SynthesisContext<SVGElement, GsapAnimation>): GsapAnimation {
    // target
    const groups = this.groups
    // timing
    const stagger = this.stagger

    const tl = gsap.timeline()
    groups.forEach((group, i) => {
      tl.add(this.generateForGroup(group, cxt), i === 0 ? 0 : ('<' + stagger))
    })
    return tl
  }

  generateForGroup(elements: SVGElement[], cxt: SynthesisContext<SVGElement, GsapAnimation>): GsapAnimation {
    // effect
    const {attr, from, to, ease} = this.props()

    let type
    if (from != null && to != null) {
      type = 'fromTo'
    } else if (from != null) {
      type = 'from'
    } else if (to != null) {
      type = 'to'
    } else {
      throw new Error('CustomImpl: Attribute "from" or "to" must be specified at least one.')
    }

    // let type = this.prop('type')
    //
    // if (type === 'auto') {
    //   if (from != null && to != null) {
    //     type = 'fromTo'
    //   } else if (from != null) {
    //     type = 'from'
    //   } else if (to != null) {
    //     type = 'to'
    //   } else {
    //     throw new Error('CustomImpl: Attribute "from" or "to" must be specified at least one.')
    //   }
    // } else {
    //   // check type and from/to
    //   if (type === 'fromTo') {
    //     assert(from != null && to != null, 'CustomImpl: Attribute "from" and "to" must be specified for fromTo animation')
    //   } else if (type === 'from') {
    //     assert(from != null, 'CustomImpl: Attribute "from" must be specified for from animation')
    //   } else if (type === 'to') {
    //     assert(to != null, 'CustomImpl: Attribute "to" must be specified for to animation')
    //   } else {
    //     throw new Error(`CustomImpl: Unknown type: ${type}`)
    //   }
    // }

    let tw: GsapAnimation

    const vars = this.mergeVars({ease})
    switch (type) {
      case 'fromTo':
        tw = gsap.fromTo(elements, {[attr]: from}, {...vars, [attr]: to})
        break
      case 'from':
        tw = gsap.from(elements, {...vars, [attr]: from})
        break
      case 'to':
        tw = gsap.to(elements, {...vars, [attr]: to})
        break
      default:
        // never goes here as type is checked
        throw new Error(`Unknown type: ${type}`)
    }

    return tw
  }
}

