<template>
  <div class="proj-selection-panel">
    <div class="upload-title">Load a chart</div>
    <el-upload
        class="drag-upload"
        drag
        style="width: 100%"
        :on-success="handleFile"
        :http-request='uploadFile'
        :before-upload="fileFilter"
        :show-file-list="false"
        :auto-upload="true"
        action="none"
        multiple>
      <i class="el-icon-upload"></i>
      <div class="el-upload__text">Drop or select file (.svg)</div>
      <!--        <div class="el-upload__tip" slot="tip">/div>-->
    </el-upload>

    <el-divider></el-divider>

    <div class="upload-title">Load example charts ({{ presetCharts.length }})</div>
    <el-scrollbar :style="{ height: customCharts.length > 0 ? '190px' : '380px'}">
      <div class="svg-group">
        <el-card v-for="project in presetCharts" :key="project.id"
                 class="svg-item clickable"
                 shadow="hover"
                 :body-style="{ padding: '0px'}"
                 @click.native="handleProjectClick(project)">
          <SvgPreviewer :project="project" :height="160" :width="240"></SvgPreviewer>
          <div style="display: flex; justify-content: center; align-items: center; height: 20px;">{{ project.label }}</div>
        </el-card>
        <div v-for="i in [...Array((3 - (presetCharts.length % 3)) % 3).keys()]"
             class="svg-item"
             :key="'empty-block-' + i"></div>
      </div>
    </el-scrollbar>

    <el-divider></el-divider>

    <div class="upload-title">Load uploaded charts ({{ customCharts.length }})</div>
    <el-scrollbar style="height: 190px;" v-if="customCharts.length > 0">
      <div class="svg-group ">
        <el-card v-for="project in customCharts" :key="project.id"
                 class="svg-item clickable"
                 shadow="hover"
                 :body-style="{ padding: '0px'}"
                 @click.native="handleProjectClick(project)">
          <SvgPreviewer :project="project" :height="160" :width="240"></SvgPreviewer>
          <div style="display: flex; justify-content: center; align-items: center; height: 20px;">{{ project.label }}</div>
        </el-card>
        <div v-for="i in [...Array((3 - (customCharts.length % 3)) % 3).keys()]"
             class="svg-item"
             :key="'empty-block-' + i"></div>
      </div>
    </el-scrollbar>

  </div>
</template>

<script>
  import {mapState} from "vuex";
  import axios from "axios";
  import SvgPreviewer from "@/components/editor/ProjSelPanel/SvgPreviewer";

  export default {
    name: 'ProjSelectionPanel',
    components: {
      SvgPreviewer,
    },
    data() {
      return {
      }
    },
    mounted() {
      this.$store.dispatch('resource/queryAllSvg')
          .then(() => {
            this.$store.commit('resource/changeStaticSvgMapSign');
          })
    },
    methods: {
      uploadFile(param) {
        this.svgUrl = URL.createObjectURL(param.file);
        axios.get(this.svgUrl)
            .then(rep => {
              this.$store.dispatch('resource/addProject', {srcId: param.file.name.split('.')[0], svgStr: rep.data});
            }).catch(error => {
              this.$message.error(error);
            })
        this.$emit('closeProjSelPanel');
      },
      handleFile(res, file) {
        // if need to store svg in backend
        console.log(res, file)
      },
      fileFilter(file) {
        const isSVG = file.type === 'image/svg+xml'
        if (!isSVG) {
          this.$message.error('Only support svg chart!');
        }
        return isSVG;
      },
      handleProjectClick(project) {
        console.log('change proj', project)
        this.$store.commit('resource/changeProjectId', project.id);
        this.$emit('closeProjSelPanel');
      },
    },
    watch: {
      staticSvgMap() {
        console.log(this.staticSvgMap);
      }
    },
    computed: {
      projectId: {
        set(value) {
          this.$store.commit('resource/changeProjectId', value);
        },
        get() {
          return this.$store.state.resource.projectId;
        }
      },
      ...mapState('resource', {
        projectList: state => state.projectList,
        staticSvgMap: state => state.staticSvgMap,
      }),
      projectGroupMap() {
        const groupMap = new Map();
        groupMap.set('Single chart', []);
        groupMap.set('Custom chart', []);
        groupMap.set('Vega Line Chart', []);
        this.projectList.forEach(project => {
          const group = project.group;
          if (group === 'Single chart' || group === 'Custom chart' || group === 'Vega Line Chart') {
            groupMap.get(group).push(project);
          }
        });

        // console.log('>>>', groupMap);
        return groupMap;
      },
      presetCharts() {
        const list1 = this.projectGroupMap.get('Single chart') ?? [];
        const list2 = this.projectGroupMap.get('Vega Line Chart') ?? [];
        return list1.concat(...list2);
      },
      customCharts() {
        return this.projectGroupMap.get('Custom chart') ?? [];
      }
    }
  }
</script>

<style scoped>
.proj-selection-panel {
  user-select: none;
}
.svg-group {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
.svg-item {
  height: 183px;
  width: 240px;
  margin-bottom: 10px;
}
.clickable {
  cursor: pointer;
}
.proj-selection-panel >>> .el-scrollbar__wrap {
  overflow-x: hidden;
}

.drag-upload >>> .el-upload-dragger, .drag-upload >>> .el-upload--text {
  width: 100%;
}
.upload-title {
  font-size: 1.2rem;
  margin-bottom: 8px;
}
</style>
